import React, { Component } from "react";
import { connect } from "react-redux";
import { Field, reduxForm } from "redux-form";
import { ReanderField, ReanderSelect } from "../../../components/helpers/field";
import { getDataSales } from "../../../actions/datamaster_action";
import { getData, getData2 } from "../../../config/axios";
import { ToastNotification } from "../../../components/helpers/notification";
import Tabel from "../../../components/helpers/tabel";
import {
  disableButtonValidasi,
  saveValidasiBeli,
  saveValidasiJual,
} from "../../../actions/hutang_action";
import { Panel, PanelHeader } from "../../../components/panel/panel";
import Loading from 'react-fullscreen-loading';

const maptostate = (state) => {
  // let hasil = parseInt(localStorage.getItem('total_harga_jual')) - parseInt(localStorage.getItem('total_harga_beli'))
  // let datausersvalidasi = localStorage.getItem('datausersvalidasi')===null ? [] : JSON.parse(localStorage.getItem('datausersvalidasi'));
  return {
    DataSales: state.datamaster.getDataSales,
    saveValidasiBeli: state.hutang.saveValidasiBeli,
    saveValidasiJual: state.hutang.saveValidasiJual,
    btnValidasi: state.hutang.disableButtonValidasi,
  };
};
class HeadValidasiTitipan extends Component {
  constructor(props) {
    super(props);
    this.state = {
      isLoading: false,
      DataValidasi: [],
      btnValidasi: false,
      kode_sales: false,
      warna :false,
    };
  }

  componentDidMount() {
    this.props.dispatch(getDataSales());
    // this.props.dispatch(disableButtonValidasi());
    if (this.props.saveValidasiJual.length === 0 && this.props.saveValidasiBeli.length === 0) {
      localStorage.setItem(
        "validasi_titipan",
        JSON.stringify([])
      );
      localStorage.setItem(
        "validasi_beli",
        JSON.stringify([])
      );
    }
  }
  getDataSales(e) {
    localStorage.setItem(
      "validasi_titipan",
      JSON.stringify([])
    );
    localStorage.setItem(
      "validasi_beli",
      JSON.stringify([])
    );
    this.setState({
      kode_sales: e,
    });
    getData("sales/get/by-kode-sales/" + e)
      .then((res) => {
        // localStorage.setItem('datausersvalidasi',JSON.stringify(res.data))
        this.props.change("nama_sales", res.data.nama_sales);
        this.props.dispatch(saveValidasiJual([]));
        this.props.dispatch(saveValidasiBeli([]));
      })
      .catch((err) => {
        ToastNotification("info", "Gagal Mengambil Data Keserver");
      });
  }
  getDetaValidasi() {
    let hasil = {
      kode_sales: this.state.kode_sales,
    };
    if (hasil.kode_sales === false) {
      ToastNotification("info", "Kode Sales Harus Di pilih");
      this.setState({
        isLoading: false,
      });
      return false;
    }
     this.setState({
      isLoading: true,
    });
    if (this.state.kode_sales === "SEMUA") {      
    getData2("titip/unvalid", hasil)
      .then((res) => {
        if (
          res.data.length === 0 
        ) {
          ToastNotification("info", "Belum Ada Data Yang Harus Di Validasi");
          this.setState({
            isLoading: false,
          });
          this.props.change('grandtotal',0)
        } else {
          let harga_total = 0;
          res.data.forEach(element => {
            harga_total = harga_total + element.harga_total
          });

          this.props.change('grandtotal',harga_total.toLocaleString("kr-KO"))
          ToastNotification("success", "Data Validasi Tersedia");
          this.setState({
            isLoading: false,
          });
          this.props.dispatch(disableButtonValidasi(true));
          localStorage.setItem(
            "validasi_titipan",
            JSON.stringify(res.data)
          );
          this.props.dispatch(saveValidasiJual(res.data));
        }
      })
      .catch((err) => {
        ToastNotification("info", "Terjadi Kesalahan Saat Request");
        this.setState({
          isLoading: false,
          DataValidasi: [],
        });
      });
    } else {
      getData2("titip/unvalid/by-sales/"+ this.state.kode_sales)
        .then((res) => {
        console.log(res);
        if (
          res.data.length === 0 
        ) {
          ToastNotification("info", "Belum Ada Data Yang Harus Di Validasi");
          this.setState({
            isLoading: false,
          });
          this.props.change('grandtotal',0)
        } else {
          let harga_total = 0;
          res.data.forEach(element => {
            harga_total = harga_total + element.harga_total
          });

          this.props.change('grandtotal',harga_total.toLocaleString("kr-KO"))
          ToastNotification("success", "Data Validasi Tersedia");
          this.setState({
            isLoading: false,
          });
          this.props.dispatch(disableButtonValidasi(true));
          localStorage.setItem(
            "validasi_titipan",
            JSON.stringify(res.data)
          );
          this.props.dispatch(saveValidasiJual(res.data));
        }
      })
      .catch((err) => {
        ToastNotification("info", "Terjadi Kesalahan Saat Request");
        this.setState({
          isLoading: false,
          DataValidasi: [],
        });
      });
    }

  }
  render() {
    const selectRow = {
      mode: "checkbox",
      clickToSelect: true,
      selectColumnPosition: "right",
      onSelect: (row, isSelect, rowIndex, e) => {
        // console.log(isSelect);
        if (isSelect === true) {
          let hasil = {
            no_titip_group: row.no_titip_group,
          };
          let cek = JSON.parse(localStorage.getItem("data_validasi_titip")) || [];
          let hasil_cek = cek.findIndex(
            (hasil) => hasil.no_titip_group === row.no_titip_group
          );
          if (hasil_cek === -1) {
            cek.push(hasil);
            localStorage.setItem("data_validasi_titip", JSON.stringify(cek));
          }
        } else {
          // console.log();
          if (localStorage.getItem("data_validasi_titip") === null) {
          } else {
            let data = JSON.parse(localStorage.getItem("data_validasi_titip"));
            data.splice(row.no_titip_group, 1);
            localStorage.setItem("data_validasi_titip", JSON.stringify(data));
          }
        }
      },
      onSelectAll: (isSelect, data, e) => {
        data.forEach((list, index) => {
          if (isSelect === true) {
            let hasil = {
              no_titip_group: list.no_titip_group,
            };
            let cek = JSON.parse(localStorage.getItem("data_validasi_titip")) || [];
            let hasil_cek = cek.findIndex(
              (hasil) => hasil.no_titip_group === list.no_titip_group
            );
            if (hasil_cek === -1) {
              cek.push(hasil);
              localStorage.setItem("data_validasi_titip", JSON.stringify(cek));
            }
          } else {
            if (localStorage.getItem("data_validasi_titip") === null) {
            } else {
              let data = JSON.parse(localStorage.getItem("data_validasi_titip"));
              data.splice(list.no_titip_group, 1);
              localStorage.setItem("data_validasi_titip", JSON.stringify(data));
            }
          }
        });
      },
    };
    let datasalses = [
      {
        kode_sales : "SEMUA",
        nama_sales: "SEMUA"
      }
    ];
    this.props.DataSales.forEach(element => {
      let row = {
        kode_sales: element.kode_sales,
        nama_sales: element.nama_sales,
      }
      datasalses.push(row)
    });

    return (
      <form onSubmit={this.props.handleSubmit} autoComplete="off"
      onKeyPress={(e) => {
        e.key === "Enter" && e.preventDefault();
      }}
      >
        <div className="row">
          <div className="col-3">
            <Field
              name="kode_sales"
              onChange={(e) => this.getDataSales(e)}
              label="Pilih Kode Sales"
              placeholder="Masukan Kode Sales"
              options={datasalses.map((list) => {
                let data = {
                  value: list.kode_sales,
                  name: list.nama_sales,
                };
                return data;
              })}
              component={ReanderSelect}
            />
          </div>
          <div className="col-3">
            <Field
              name="nama_sales"
              component={ReanderField}
              type="text"
              label="Nama Sales"
              placeholder="Masukan Nama Sales"
              readOnly={true}
            />
          </div>
          <div className="col-3">
            <Field
              name="grandtotal"
              component={ReanderField}
              type="text"
              label="Grand Total"
              placeholder="Grand Total"
              readOnly={true}
              customeCss={this.state.warna ? "customeInput" : ""}
            />
          </div>
          <div className="col-3">
            <label>&nbsp;</label>
            <button
              onClick={() => this.getDetaValidasi()}
              className="btn btn-primary btn-block"
              disabled={this.state.isLoading}
              type="button"
            >
              {this.state.isLoading ? (
                <>
                  <i className="fas fa-spinner fa-spin"></i> &nbsp; Cari Data
                </>
              ) : (
                "Cari Data"
              )}
            </button>
          </div>
          {/* <div className="col-3">
          <Field
              name="grand_total"
              component={ReanderField}
              type="text"
              label="Grand Total"
              placeholder="Grand Total"
              readOnly={true}
            />
          </div> */}
          <div className="col-12">
            <Panel>
              <PanelHeader>List Data Titipan</PanelHeader>
              <br />
              <Tabel
                selectRow={selectRow}
                keyField="kode_barcode"
                columns={this.props.columnsJual}
                data={this.props.saveValidasiJual || []}
                empty={true}
              />
            </Panel>
          </div>
          <div className="col-6">
            {this.props.btnValidasi === false ? (
              <button
                className="btn btn-primary btn-block"
                disabled={true}
                type="button"
              >
                Validasi
              </button>
            ) : (
              <button
                className="btn btn-primary btn-block"
                disabled={this.props.isLoading}
                type="submit"
              >
                {this.props.isLoading ? (
                  <>
                    <i className="fas fa-spinner fa-spin"></i> &nbsp; Validasi
                  </>
                ) : (
                  "Validasi"
                )}
              </button>
            )}
          </div>
          <div className="col-6">
            <button
              className="btn btn-warning btn-block"
              disabled={this.props.isLoadingBtl}
              onClick={this.props.btnClear}
              type="button"
            >
              {this.props.isLoadingBtl ? (
                <>
                  <i className="fas fa-spinner fa-spin"></i> &nbsp; Batal
                </>
              ) : (
                "Batal"
              )}
            </button>
          </div>
        </div>
        {this.props.isLoading ? (
        <Loading 
        loading
        background="rgba(0, 0, 0, 0.35)"
         loaderColor="rgba(94, 147, 117, 1)" />
      ) : null}
      </form>
    );
  }
}

HeadValidasiTitipan = reduxForm({
  form: "HeadValidasiTitipanHutang",
  enableReinitialize: true,
})(HeadValidasiTitipan);
export default connect(maptostate)(HeadValidasiTitipan);
